.input-box {
  .input-box();
  width: 100%;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  outline: none;
  transition: border-color 0.15s ease-in-out;

  &:hover {
    border-color: @special-grey;
  }

  &:focus {
    border-color: @dark-grey;
  }

  &:invalid:not(:focus):not(:placeholder-shown) {
    border-color: @status-error;
  }
}
